<template>
    <div class="header-container">
        <div class="pc">
            <div class="header-bar">
                <div class="logo" @click="setActiveTag(0, 'home')">
                    <div class="company-616"></div>
                    <!-- <img src="@/assets/logo/616companyLogo.png" alt="" width="120" heigth="50"
                        @click="setActiveTag(0, 'home')"> -->
                </div>
                <ul class="header-bar-ul">
                    <li @click="setActiveTag(1, 'aboutUs')" :class="activeTab === 1 ? 'active-tag' : ''">
                        {{ internationalDict.aboutUs }}</li>
                    <li @click="setActiveTag(2, 'hotSale')" :class="activeTab === 2 ? 'active-tag' : ''">
                        {{ internationalDict.hotSale }}</li>
                    <!-- <li @click="setActiveTag(3, 'takeAway')" :class="activeTab === 3 ? 'active-tag' : ''">
                        {{ internationalDict.takeAway }}</li> -->
                    <li @click="setActiveTag(4, 'address')" :class="activeTab === 4 ? 'active-tag' : ''">
                        {{ internationalDict.address }}</li>
                    <li @click="setActiveTag(5, 'contactUs')" :class="activeTab === 5 ? 'active-tag' : ''">
                        {{ internationalDict.contactUs }}</li>
                    <li @click="setActiveTag(6, 'joinUs')" :class="activeTab === 6 ? 'active-tag' : ''">
                        {{ internationalDict.joinUs }}</li>
                </ul>

                <div class="international">
                    <span class="pointer" @click="setLangure('chinese')">繁中</span>
                    <span>|</span>
                    <span class="pointer" @click="setLangure('english')">EN</span>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'header-bar',
    components: {

    },
    mixins: [],
    props: {

    },
    data() {
        return {
            activeTab: -1,
            lang: 1,
        }
    },
    computed: {
        internationalDict() {
            return this.$store.getters.getInternationalDict
        }
    },
    watch: {

    },
    mounted() {
        //  this.internationalDict = this.$store.getters.getInternationalDict
    },
    methods: {
        setActiveTag(index, name) {
            this.activeTab = index
            if (name) {
                this.$router.push({ name }).catch(() => { })
            }
        },
        setLangure(flag) {
            this.$store.dispatch('commitInternationalStatus', flag)
        }
    }
};
</script>
<style scoped>
.header-bar {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 5px 25px;
}
.logo{
    flex:1
}
.company-616{
    background-image: url('../assets/logo/616companyLogo.png');
    width: 20vw;
    height: 50px;
    background-size: 80%;
    background-repeat: no-repeat;
    background-position:0 50%;
    cursor: pointer;
}
.international {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-left: 3em;
}

.header-bar-ul {
    display: flex;
    list-style: none;
    align-items: center;
    gap: 2em;
}

.header-bar-ul>li {
    font-weight: 600;
    position: relative;
    cursor: pointer;
    white-space: normal;
    color: var(--text-color-gary);
    font-size: var(--title-font-size);
}

.active-tag {
    color: var(--primary-color) !important;
    border-bottom: 2px solid var(--primary-color) !important;
}
</style>